@import "../../globalStyles/mixins";
@import "../../globalStyles/variables";
@import "../../globalStyles/animations";

.about {
  color: $color3;
  box-sizing: border-box;
  position: relative;
  transition: all 500ms ease;
  @include mediaMax($tablet) {
    @include flexBox(center, center);
  }
  @include mediaMin($laptop) {
    padding-top: 15vh;
  }

  &.active {
    .divider {
      svg {
        height: 30vh;
      }
    }

    .content {
      opacity: 1;

      h2 {
        letter-spacing: 0.2em;
      }
    }
  }

  .divider {
    z-index: 0;
    position: absolute;
    bottom: -11vh;
    left: 0;
    width: 100vw;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);

    svg {
      position: relative;
      display: block;
      width: calc(100% + 1.3px);
      height: 20vh;
      fill: $color2;
      transition: all 1s ease-out;
    }
  }

  .content {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 1;
    font-weight: 600;
    transition: all 500ms ease-out;

    @include mediaMax($tablet) {
      @include flexBox(space-between, center);
      flex-wrap: wrap;
      width: 90%;
      height: 65%;
    }

    @include mediaMin($laptop) {
      left: 10vw;
    }

    h2 {
      position: relative;
      text-align: center;
      letter-spacing: 0.7em;
      transition: letter-spacing 800ms ease;
      margin-bottom: 0.2em;
      width: 100%;
      @include mediaMin($laptop) {
        width: 48%;
      }
    }

    h3,
    h2 {
      color: white;
    }

    .cv {
      display: inline-block;
      margin-top: 0.1em;
      background-color: $color3;
      color: $color1;
      text-decoration: none;
      padding: 0.5em;
      transition: all 200ms ease;
      @include borderRadius(3px);

      &:hover {
        transform: scale(1.03);
        color: $color1;
        background-color: white;
      }

      &:active {
        transform: scale(0.98);
      }
    }

    p {
      text-align: justify;
      margin: 0.8em 0;

      a {
        text-decoration: none;
        color: $color2;
        margin: 0 6px;
        transition: color 200ms ease;

        &:hover {
          border-bottom: 2px solid $color2;
          transform: scale(2);
        }
      }

      .hover-wrapper {
        margin: 0 0.3em;

        .hover-img {
          display: none;
        }

        @include mediaMin($laptop) {
          color: $color2;
          cursor: none;

          .hover-img {
            display: block;
            transform: translate(-50%, -50%);
            max-width: 400px;
            position: absolute;
            opacity: 0;
            transition: opacity 300ms ease;
            pointer-events: none;
          }

          &:hover {
            .hover-img {
              opacity: 1;
            }
          }
        }
      }
    }

    .left {
      float: left;
      width: 24%;

      .learning {
        display: flex;

        @include mediaMax($smPhone) {
          width: min-content;
        }

        .dotdotdot {
          position: relative;
          @include flexBox(center, flex-end);
          margin: 1em 0.4em;

          .dot {
            margin: 0px 2.5px;
            position: relative;
            aspect-ratio: 1/1;
            background: $color3;
            display: inline-block;
            border-radius: 50%;
            right: 0;
            bottom: 0;
            color: transparent;
            animation: jump 1.3s infinite;
            width: 4px;
            height: 4px;

            @include mediaMax($phone) {
              width: 2px;
              height: 2px;
            }

            &:first-of-type {
              animation-delay: 100ms;
            }

            &:nth-of-type(2) {
              animation-delay: 200ms;
            }
            &:nth-of-type(3) {
              animation-delay: 300ms;
            }
          }
        }
      }
    }

    .right {
      float: right;
      width: 73%;
    }

    @include mediaMax($tablet) {
      .left,
      .right {
        width: 48%;
        height: 100%;
      }
    }
  }
}

.skillsOrbit {
  position: relative;
  float: right;
  z-index: 0;
  display: none;

  @include mediaMin($laptop) {
    display: block;
    shape-outside: circle(50%);
    margin-left: 4rem;
  }

  .orbit {
    position: relative;
    @include flexBox(center, center);
    z-index: 0;

    .axis {
      box-sizing: border-box;
      position: relative;
      border: 4px dotted white;
      border-radius: 50%;
      @include flexBox(center, center);
      width: 80vh;
      height: 80vh;
      animation: 300s spin linear infinite;

      .axis {
        box-sizing: border-box;
        width: 55vh;
        height: 55vh;
        animation: 200s spin linear infinite;

        .axis {
          box-sizing: border-box;
          width: 30vh;
          height: 30vh;
          animation: 100s spin linear infinite;
        }
      }

      .logoWrapper {
        position: absolute;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        width: min-content;
        transform: rotate(25deg);
        animation: 300s reverseSpin linear infinite;

        .logo {
          position: relative;
          z-index: 9;
          width: 40px;
          height: 40px;

          svg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            overflow: hidden;
            line-height: 0;
            fill: $color3;
          }

          span {
            position: absolute;
            display: inline-block;
            right: 5px;
            top: 6px;
            height: 15px;
            width: 15px;
            margin-bottom: 6px;
            background-color: $color3;
            border-radius: 50%;
          }
        }
      }
    }

    .axis2 {
      box-sizing: border-box;
      position: absolute;
      border: 4px dotted $color3;
      border-radius: 50%;
      @include flexBox(center, center);
      width: 20vh;
      height: 20vh;
      left: -13%;
      animation: 60s reverseSpin linear infinite;

      img {
        &:first-of-type {
          animation: 60s spin linear infinite;
        }
      }
    }

    .icon {
      position: absolute;
      bottom: -7%;

      &.html {
        bottom: -10%;
      }
    }
    .subIcon {
      position: absolute;
      bottom: -20%;
    }
  }
  img {
    max-height: 40px;
    width: auto;
  }
}
@keyframes jump {
  0% {
    bottom: 0px;
  }
  20% {
    bottom: 3px;
  }
  40% {
    bottom: 0px;
  }
}
